@font-face {
  font-family: "icomoon";
  src: url("./icomoon.eot?tyhwhc");
  src: url("./icomoon.eot?tyhwhc#iefix") format("embedded-opentype"),
    url("./icomoon.ttf?tyhwhc") format("truetype"),
    url("./icomoon.woff?tyhwhc") format("woff"),
    url("./icomoon.svg?tyhwhc#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */

  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-email:before {
  content: "\e900";
}
.icon-github:before {
  content: "\e901";
}
.icon-linkedin:before {
  content: "\e902";
}
